const getters = {
  terms: (state) => {
    return state.terms[state.lang];
  },
  isLtr: (state) => {
    return state.isLtr;
  },
  lang: (state) => {
    return state.lang;
  },
  langs: (state) => {
    return state.languages;
  },
};
const mutations = {
  setLang: (state, lang) => {
    state.lang = lang;
  },
  updateDirection: (state, isLtr) => {
    state.isLtr = isLtr;
  },
};
export default {
  state,
  getters,
  mutations,
};
