import Vue from "vue";
import Router from "vue-router";
Vue.use(Router);

const Resume = () => import("./../views/Resume.vue");

export default new Router({
  mode: "history", // https://router.vuejs.org/api/#mode
  linkActiveClass: "open active",
  scrollBehavior: () => ({ y: 0 }),

  routes: [
    {
      path: "/",
      component: Resume,
    },
    { path: "/", redirect: "/" },
  ],
});
